.text {
	line-height: 1.5;
	font-size: 16px;
	@include r(1200) {
		font-size: 14px;
	}
	&--center {
		text-align: center;
		img {
			margin-left: auto;
			margin-right: auto;
		}
	}
	p {
		margin-bottom: 1em;
	}
	// blockquote {
	// 	margin-bottom: 1rem;
	// 	padding-left: 15px;
	// 	border-left: 2px solid #CAD4D4;
	// 	font-style: italic;
	// }
	h3 {
		font-weight: 700;
		margin-bottom: 1rem;
		font-size: 28px;
	}
	h4 {
		font-weight: 700;
		margin-bottom: 1rem;
		font-size: 24px;
	}
	h5 {
		font-weight: bold;
		font-size: 20px;
		margin-bottom: 1rem;
	}
	h6 {
		font-weight: bold;
		margin-bottom: 1rem;
	}
	strong, b {
		font-weight: bold;
	}
	a {
		color: $green;
		&:hover {
			text-decoration: none;
		}
	}
	i {
		font-style: italic;
	}
	iframe {
		display: block;
		max-width: 948px;
		width: 100%;
		margin-bottom: 2rem;
		height: 480px;
		margin-top: 2rem;
	}
	picture {
		padding-right: 140px;
		display: block;
		@include r(1200) {
			padding-right: 0px;
		}
	}
	img {
		display: block;
		max-width: 100%;
		width: auto;
		margin-bottom: 2rem;
		margin-top: 2rem;
	}
	ul {
		list-style-type: square;
		margin-bottom: 1em;
		// line-height: 2;
		font-size: inherit;
		padding-left: 40px;
		@include r(1200) {
			list-style-position: inside;
			padding-left: 0px;
		}
		color: $green;
		span {
			color: $dark;
		}
	}
	li {
		margin-bottom: 1rem;
	}
	// ol {
	// 	list-style-type: decimal;
	// 	list-style-position: inside;
	// 	margin-bottom: 2em;
	// 	line-height: 2;
	// 	font-size: inherit;
	// }
	.btn {
		color: $light;
		padding-left: 53px;
		padding-right: 53px;
		margin-bottom: 1em;
	}
}

.text-green {
	color: $green;
}

.marker-block {
	display: inline-flex;
	align-items: center;
	@include r(1200) {
		display: flex;
		margin-bottom: 15px;
	}
	svg {
		flex-shrink: 0;
		display: block;
		width: 26px;
		height: 26px;
		margin-right: 12px;
		@include r(1200) {
			width: 22px;
			height: 22px;
			margin-right: 15px;
		}
	}
	span, a {
		text-decoration: none;
		color: $dark;
		font-size: 16px;
	}
	a {
		&:hover {
			text-decoration: underline;
		}
	}
}